import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Separator from '@vkontakte/vkui/dist/components/Separator/Separator';
import Icon28FireOutline from '@vkontakte/icons/dist/28/fire_outline';
import Counter from '@vkontakte/vkui/dist/components/Counter/Counter';
import List from '@vkontakte/vkui/dist/components/List/List';
import Group from '@vkontakte/vkui/dist/components/Group/Group';

const osName = platform();

const Vsekateg = ({ id, go }) => (
	<Panel id={id}>
			<PanelHeader
			left={<PanelHeaderButton onClick={go} data-to="home">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</PanelHeaderButton>}
		>
			Категории 
		</PanelHeader>
        <Group>
        <List>
		<Cell expandable before={<Icon28FireOutline fill='#ff4d29'/>} onClick={go} data-to="forschool" indicator={<Counter mode="primary">2</Counter>}>Лайфхаки для школы</Cell> 
                <Separator />
				<Cell expandable onClick={go} data-to="forhome" indicator={<Counter mode="primary">2</Counter>}>Лайфхаки для дома</Cell>
                <Separator />
                <Cell expandable onClick={go} data-to="forsummer" indicator={<Counter mode="primary">0</Counter>}>Лайфхаки для лета</Cell> 
                <Separator />
                <Cell expandable indicator={<Counter>В разработке</Counter>}>Лайфхаки для машины</Cell>
                <Separator />
                <Div style={{ paddingTop: 0, paddingBottom: 0, color: 'grey' }}>
				<p align="center">Скоро тут появятся новые категории.</p>
				</Div>
                <Separator />
                </List>
                </Group>
	</Panel>
);

Vsekateg.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Vsekateg;
